.user{
  padding: 15px;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.userInfo{
    width: 50%;
    display: flex;
    flex-direction: row;
    gap: 10px;
}
.name{
    align-self: center;
    justify-items: center;
    font-size: 18px;
    font-weight: bold;
    color: white;
    font-family: Lato;
    user-select: none;
}
.userIcon{
    width: 35px;
    height: 35px;
    justify-items: flex-start;
    border: 1px solid black;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #efefef;
}
.Icon{
    padding: 5px;
}

.userButton{
    height: 100%;
    width: 50%;
    display: flex;
    flex-direction: row;
    gap: 20px;
    align-items: center;
    justify-content:flex-end;
}