*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70%;
  width: 80%;
}
.contenedorPrincipal{
  border-radius: 5px;
  border: 2px solid black;
  min-width: 350px;
  width: 30%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url("./imgenes/whatspp.jpg");
}
.contenedorUser{
  width: 100%;
  height: 10%;
  background-color: #37A09b;

}
.contenedorInfo{
  width: 100%;
  height: 80%;
  padding: 5px;
  overflow-y: visible; /*Provee barra de despazamiento si hay contenido excedente */
  overflow-x: hidden;
}
::-webkit-scrollbar {
  width: 0.1px;
}
.contenedorInput{
  width: 100%;
  height: 10%;
}