.input-wrapper{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-end;
    gap: 10px;
    padding:2px 3px;
}
input {
    width: 89%;
    height: 60%;
    border-radius: 15px;
    background-color: #373f51;
    color: white;
    
}
.inputIcon{
    width: 11%;
    height: 35px;
    justify-items: flex-start;
    border: none;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color:#37A09b;
}
.Icon{
    padding: 5px;
    transform: scale(1.5);
}
   